import { SHA256 } from "./util";

const axios = require('axios')

let token = localStorage.getItem('instapay_token')
//let token = null
//const authorization = `Bearer ${token}`

const API_PATH = 'https://api.instapay.kr'
const LOGIN_PATH = 's1/login'
const TRAS_PATH = 's1/tras'

let login = (u) => {
    const url = `${API_PATH}/${LOGIN_PATH}`
    
    const user = { "wid":u.userid, "wpw":u.password, }

    return axios.request({
        url,
        method:'get',
        params: {
            aid: 'd20ah-ol17w-03p30-15b05-e05dc',
            pack: SHA256(JSON.stringify(user))
        }
    })
}

let loadToken = () => {
    if(token===null)
        token = localStorage.getItem('instapay_token')
    return (token !== null)
}

let tras = (qe, qb, pmethod, state) => {
    // if(!loadToken()) {
    //     console.log("token error");
    //     return;
    // }

    console.log('set tras:', qe,qb, token)
    const url = `${API_PATH}/${TRAS_PATH}`

    return axios.request({
        url,
        method: 'get',
        params: {
            qe,
            qb,
            pmethod,
            state,
            limit: 500
        },
        headers: {
            //authorization
            authorization: `Bearer ${token}`
        }
    })
}
  

export default {
    login,
    tras,
    loadToken
}